.card {
    width: 100%;
    display: flex;
    flex-direction: row;
    border: 1px solid #ddd;
    border-radius: 4px;
    overflow: hidden;
  }
  
  .card-image {
    width: 40%;
  }
  
  .card-image img {
    width: 100%;
    height: auto;
  }
  
  .card-content {
    padding: 16px;
    width: 60%;
    font-size: medium;
  }
  
  .card-title {
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 8px;
  }
  
  .card-price {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 16px;
  }
  
  .card-description {
    margin-bottom: 16px;
  }
  
  .card-features {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .card-features li {
    margin-bottom: 8px;
  }